* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', 'Arial', sans-serif;
}
@font-face {
  src: url('../fonts/Lato-Bold.woff2') format('woff2'), url('../fonts/Lato-Bold.woff') format('woff');
  font-weight: 700;
  font-family: Lato;
  font-display: swap;
}
@font-face {
  src: url('../fonts/Lato-Regular.woff2') format('woff2'), url('../fonts/Lato-Regular.woff') format('woff');
  font-weight: 400;
  font-family: Lato;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
.body {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  position: relative;
}
.body--index {
  display: flex;
  flex-direction: column;
}
.body--menu-open {
  overflow: hidden;
}
.body--menu-open::before {
  content: '';
  z-index: 2;
  position: fixed;
  width: 100%;
  max-width: 100vw;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
}
.header {
  width: 100%;
  z-index: 3;
  background-color: #ffffff;
}
.header--fixed {
  position: fixed;
  top: 0;
  background-color: transparent;
}
.header--fixed .header__wrap {
  padding: 20px;
}
.header__wrap {
  position: relative;
  display: grid;
  grid-template-columns: max-content 1fr;
  align-self: center;
  padding: 80px 20px 100px 20px;
}
@media (max-width: 900px) {
  .header__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
  }
}
.header__index-link {
  z-index: 1;
}
.header__logo {
  width: 150px;
  height: 42px;
  object-fit: cover;
  z-index: 1;
  margin-left: -10px;
}
.header__menu-button {
  width: 80px;
  height: 80px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  display: none;
  margin-right: -20px;
}
@media (max-width: 900px) {
  .header__menu-button {
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.ham {
  cursor: pointer;
  transition: transform 400ms;
  user-select: none;
}
.hamRotate.active {
  transform: rotate(45deg);
}
.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #000000;
  stroke-width: 5.5;
  stroke-linecap: round;
}
.ham7 .top {
  stroke-dasharray: 40 82;
}
.ham7 .middle {
  stroke-dasharray: 40 111;
}
.ham7 .bottom {
  stroke-dasharray: 40 161;
}
.ham7.active .top {
  stroke-dasharray: 17 82;
  stroke-dashoffset: -62px;
}
.ham7.active .middle {
  stroke-dashoffset: 23px;
}
.ham7.active .bottom {
  stroke-dashoffset: -83px;
}
.nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
@media (max-width: 900px) {
  .nav {
    position: absolute;
    left: 0;
    right: 0;
    top: -200%;
    transition: all 0.5s ease-in;
  }
}
.nav--open {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transition: all 0.5s ease;
  justify-content: center;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
}
.nav--open .nav__list {
  padding: 80px 20px;
  pointer-events: all;
  opacity: 1;
  transition: all 0.5s ease;
}
.nav--open .nav__link {
  color: #1a1c22;
}
.nav--open .nav__link::after {
  background-color: #1a1c22;
}
.nav__list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
  list-style: none;
}
@media (max-width: 900px) {
  .nav__list {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
  }
}
@media (max-width: 900px) {
  .nav__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}
.nav__link {
  padding: 8px 0;
  position: relative;
  font-family: 'Lato', 'Arial', sans-serif;
  color: #1a1c22;
  font-weight: 500;
  font-size: 18px;
  text-decoration: none;
  user-select: none;
  opacity: 0.7;
  transition: 0.2s all ease-in;
}
.nav__link::after {
  display: none;
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 1px;
  background-color: #1a1c22;
  transition: 0.2s all ease-in;
  border-radius: 20%;
}
.nav__link:hover,
.nav__link:focus {
  opacity: 1;
  transition: 0.2s all ease-in;
}
.nav__link:hover::after,
.nav__link:focus::after {
  display: block;
}
.nav__link--index-page {
  color: #ffffff;
}
.nav__link--index-page::after {
  background-color: #ffffff;
}
.nav__link--current {
  opacity: 1;
  pointer-events: none;
}
.nav__link--current::after {
  display: block;
}
@media (max-width: 900px) {
  .nav__link {
    width: 100%;
    font-size: 22px;
    text-align: center;
  }
}
.main {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}
.sliderfull {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  position: relative;
}
.sliderfull-items__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 50px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 1;
}
.sliderfull-items__button--next {
  right: 10px;
}
.sliderfull-items__button--prev {
  left: 10px;
}
.sliderfull-items__button-img {
  width: 100%;
  height: 100%;
}
.sliderfull-items__button-img:hover {
  opacity: 0.7;
}
.sliderfull-items__button-img:active {
  opacity: 0.3;
}
.sliderfull-indicators {
  z-index: 1;
  max-width: 350px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.sliderfull-indicators__item {
  list-style: none;
}
.sliderfull-indicators__button {
  margin: 0 3px;
  width: 40px;
  height: 10px;
  border: none;
  border-radius: 8px;
  background-color: #ffffff;
  opacity: 0.5;
  transition: 0.4s ease;
  cursor: pointer;
}
.sliderfull-indicators__button--active {
  background-color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.sliderfull-indicators__button:hover,
.sliderfull-indicators__button:focus {
  opacity: 0.8;
}
.sliderfull-indicators__button:active {
  transition: 0s;
  opacity: 1;
}
.sliderfull-items {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.sliderfull-items__inner {
  width: 100%;
  height: 100%;
  display: flex;
  transition: 1s all;
}
.sliderfull-items__item {
  text-align: center;
}
.sliderfull-items__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.contacts {
  width: 100%;
}
.contacts__map {
  width: 100%;
  height: 500px;
  display: flex;
}
@media (max-width: 900px) {
  .contacts__map {
    height: 350px;
  }
}
.contacts__wrap {
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 10px;
}
.contacts__info {
  padding: 80px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
}
.contacts__city {
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 20px;
  font-weight: 400;
  color: #1a1c22;
}
.contacts__address {
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: #1a1c22;
}
.contacts__tel {
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: #3d3a50;
  opacity: 0.8;
}
.contacts__tel:hover,
.contacts__tel:focus {
  color: #1a1c22;
  opacity: 1;
}
.our-office {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 300px min-content min-content;
  grid-row-gap: 30px;
  padding-bottom: 40px;
  background-color: #ffffff;
}
.our-office__banner {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 20px;
}
.our-office__banner-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.our-office__about {
  width: 100%;
}
.our-office__about-wrap {
  padding: 0 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content;
  grid-row-gap: 15px;
}
.our-office__item-title {
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 19px;
  font-weight: 600;
  color: #1a1c22;
  text-transform: none;
  text-align: left;
}
.our-office__about-text {
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 17px;
  font-weight: 400;
  color: #1a1c22;
  text-transform: none;
  text-align: left;
}
.our-office__text-block {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 5px;
}
.our-office__persons-list {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 8px;
}
.our-office__persons-item {
  position: relative;
  display: flex;
}
.our-office__persons-item:not(:last-child)::after {
  content: ',';
  position: absolute;
  top: 0;
  right: -5px;
  height: min-content;
}
.our-office__persons-item:last-child::after {
  content: '.';
  position: absolute;
  top: 0;
  right: -5px;
  height: min-content;
}
.projects {
  list-style: none;
  padding: 0 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  grid-column-gap: 30px;
  grid-row-gap: 100px;
  padding-bottom: 150px;
}
@media (max-width: 900px) {
  .projects {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 600px) {
  .projects {
    grid-template-columns: 1fr;
  }
}
.projects__card {
  position: relative;
  width: 100%;
  height: 100%;
  aspect-ratio: 1.5;
}
.projects__link {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  width: 100%;
  height: 100%;
  text-decoration: none;
}
.projects__info {
  width: 100%;
  position: absolute;
  bottom: -50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.projects__title {
  font-family: 'Lato', 'Arial', sans-serif;
  color: #1a1c22;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
}
@media (max-width: 900px) {
  .projects__title {
    font-size: 14px;
    font-weight: 400;
  }
}
.projects__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: flex;
}
.project {
  display: grid;
  grid-template-columns: 1.3fr 0.7fr;
  grid-row-gap: 40px;
  padding: 0 20px;
}
@media (max-width: 900px) {
  .project {
    grid-template-columns: 1fr;
    grid-template-rows: min-content 1fr;
  }
}
.project__gallery {
  width: 100%;
}
@media (max-width: 900px) {
  .project__gallery {
    grid-row-start: 2;
  }
}
.project__info {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-end;
  align-items: flex-start;
  gap: 40px;
  padding-left: 20px;
}
@media (max-width: 900px) {
  .project__info {
    padding-left: 0;
    padding-top: 80px;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, min-content);
  }
}
.project__title {
  text-align: left;
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 20px;
  color: #1a1c22;
  font-weight: 500;
  order: 1;
}
.project__project-dexcription {
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: #1a1c22;
  line-height: 32.4px;
}
.project-data-list {
  position: sticky;
  top: 20px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  gap: 10px;
  list-style: none;
}
@media (max-width: 900px) {
  .project-data-list {
    position: static;
  }
}
.project-data-list__item {
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 5px;
  justify-items: center;
}
.project-data-list__title {
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 17px;
  font-weight: 600;
  color: #1a1c22;
  text-transform: capitalize;
  white-space: nowrap;
}
.project-data-list__description {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 17px;
  font-weight: 400;
  color: #1a1c22;
}
.project-gallery {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 20px;
}
.project-gallery__item {
  width: 100%;
  list-style: none;
}
.project-gallery__image {
  object-fit: contain;
  width: 100%;
  height: max-content;
  display: flex;
}
.footer {
  width: 100%;
  background-color: #ffffff;
}
.footer__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 40px 20px;
}
@media (max-width: 900px) {
  .footer__wrap {
    flex-direction: column;
    justify-content: flex-start;
    gap: 20px;
  }
}
.social-list {
  list-style: none;
  display: flex;
  gap: 10px;
  align-items: center;
}
.social-list__item {
  position: relative;
  --borderWidth: 2px;
  border-radius: 50%;
}
.social-list__item:active::before {
  content: '';
  position: absolute;
  top: calc(-1 * var(--borderWidth));
  left: calc(-1 * var(--borderWidth));
  height: calc(100% + var(--borderWidth) * 2);
  width: calc(100% + var(--borderWidth) * 2);
  background: linear-gradient(60deg, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82);
  border-radius: 50%;
  animation: animatedgradient 3s ease alternate infinite;
  background-size: 300% 300%;
}
.social-list__link {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.15s ease;
}
.social-list__link:hover,
.social-list__link:focus {
  color: #ffffff;
}
.social-list__link:hover .social-list__social-name,
.social-list__link:focus .social-list__social-name {
  visibility: visible;
  opacity: 1;
  transform: translate(-50%, -150%);
}
.social-list__link--instagram {
  background: #527fa6;
  color: #ffffff;
}
.social-list__link--instagram .social-list__social-name {
  background: #527fa6;
  color: currentcolor;
}
.social-list__link--instagram .social-list__social-name::after {
  border-top-color: #527fa6;
}
.social-list__link--facebook {
  background: #3b5a9b;
  color: #ffffff;
}
.social-list__link--facebook .social-list__social-name {
  background: #3b5a9b;
  color: currentcolor;
}
.social-list__link--facebook .social-list__social-name::after {
  border-top-color: #3b5a9b;
}
.social-list__link--email {
  background: #000;
  color: #ffffff;
}
.social-list__link--email .social-list__social-name {
  background: #000;
  color: currentcolor;
}
.social-list__link--email .social-list__social-name::after {
  border-top-color: #000;
}
.social-list__social-name {
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  padding: 10px 12px;
  border-radius: 40px;
  opacity: 0;
  pointer-events: none;
  text-transform: uppercase;
  transform: translate(-50%, -100%);
  transition: all 0.3s ease;
  z-index: 1;
  font-family: 'Lato', 'Arial', sans-serif;
  font-size: 12px;
  font-weight: 400;
  white-space: nowrap;
}
.social-list__social-name::after {
  content: '';
  position: absolute;
  display: block;
  bottom: 1px;
  left: 50%;
  width: 0;
  height: 0;
  border: solid;
  border-width: 10px 10px 0 10px;
  border-color: transparent;
  transform: translate(-50%, 100%);
}
.social-list__icon {
  width: 25px;
  height: 25px;
  fill: #ffffff;
}
